import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
import DatePicker from "react-datepicker";

class CetakLaporanExportBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tgl_awal: new Date(),
      tgl_akhir: new Date(),
    };
  }
  tgl_awalChange = (date) => {
    this.setState({
      tgl_awal: date,
    });
  };
  tgl_akhirChange = (date) => {
    this.setState({
      tgl_akhir: date,
    });
  };
  render() {
    return (
      <div className="row">
        <div className="col-lg-5">
          <label> Tanggal Dari </label>
          <div className="customDatePickerWidth">
            <DatePicker
              onChange={this.tgl_awalChange}
              dateFormat="yyyy-MM-dd"
              className="form-control"
              selected={this.state.tgl_awal}
            />
          </div>
        </div>
        <div className="col-lg-4">
          <label>Sampai Tanggal </label>
          <div className="customDatePickerWidth">
            <DatePicker
              onChange={this.tgl_akhirChange}
              dateFormat="yyyy-MM-dd"
              className="form-control"
              selected={this.state.tgl_akhir}
            />
          </div>
        </div>
        <div className="col-lg-3">
          <div className="text-right">
            <label>&nbsp;</label>
            <button className="btn btn-primary btn-block">
              Cetak Laporan Kirim Barang <i className="fa fa-print ml-3"></i>
            </button>
          </div>
        </div>
      </div>
    );
  }
}

CetakLaporanExportBarang = reduxForm({
  form: "CetakLaporanExportBarang",
  enableReinitialize: true,
})(CetakLaporanExportBarang);
export default connect()(CetakLaporanExportBarang);
